<!-- 数据查询 -->
<template>
	<div class="contain">
		<el-aside width="200px" class="side">
			<span class="icon iconfont menu-icon">&#xe603;</span>
			<div @click="menuOpend = !menuOpend" class="title"><span :class="menuOpend ? 'el-icon-caret-bottom' : 'el-icon-caret-top'"></span>数据查询</div>
			<div class="menu-box">
				<transition name="test-fead">
					<ul v-if="menuOpend">
						<li @click="changeActive(index)" :class="{ active: curIndex == index }" v-for="(v,index) in menuList">
							<span v-if="index=='0'" class="icon iconfont">&#xe6e8;</span>
							<span v-if="index=='1'" class="icon iconfont">&#xe604;</span>
							{{ v.text }}
						</li>
					</ul>
				</transition>
			</div>
		</el-aside>
		<router-view></router-view>
	</div>
</template>

<script>
export default {
	name: 'dataSearch',
	data() {
		return {
			menuList: [
			{
				text: '停车点明细'
			}
			],
			menuOpend: true,
			downShow: true,
			curIndex: '0'
		}
	},
	created() {

	},
	mounted() {

	},
	methods: {
		changeActive(index){
			this.curIndex = index;
			switch(index){
				case 0:
				this.$router.push('/home/dataSearch/parkInfo');
				break;
				default:
			}
		}
	}
}
</script>

<style scoped>
.contain{display: flex;background: url(../../assets/image/carManag/sideBg.png) repeat-y;flex: 1;}
.contain .side{width: 200px;}
.side .menu-icon{font-size: 14px;color: #fff;text-align: center;background: #4a5064;height: 30px;line-height: 30px;display: block;}
.side .title{height: 40px;line-height: 40px;padding-left: 12px;color: #fff;font-size: 14px;background: #00a8d4;display: block;}
.side .title span{cursor: pointer;}
.side ul li{color: #fff;padding-left: 12px;height: 40px;line-height: 40px;cursor: pointer;}
.side ul li span{font-size: 16px;color: #9ca3ac;display: inline-block;width: 30px;}
.side ul .active{background: #42485b;}
.menu-box{overflow: hidden;}
.test-fead-enter-active,.test-fead-leave-active{
	transition: linear .5s;
}
.test-fead-enter,.test-fead-leave-to{margin-top: -100%;}
</style>


